.report-block {
	margin-bottom: 40px;
	border: solid 1px #e0e0e0;
	padding: 25px 30px 35px;
	&__title {
		margin-bottom: 12px;
	}
	&__title-link {
		.dinpro-m();
		.font-size(14);
		line-height: 1.36;
		text-align: left;
		color: #000000;
		text-decoration: none;
	}
	&__date {
		.dinpro-m();
		.font-size(14);
		line-height: 1.36;
		text-align: left;
		color: #000000;
	}
	&__content {
		margin-bottom: 10px;
	}
	&__row {
		padding: 6px 0 3px;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		flex-wrap: wrap;
		border-bottom: dashed 1px rgba(151,151,151,.4);
	}
	&__name {
		.regular();
		.font-size(14);
		letter-spacing: 0.6px;
		text-align: left;
		color: #000000;
		width: 138px;
		padding-right: 10px;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	&__money {
		.regular();
		.font-size(14);
		letter-spacing: 0.6px;
		text-align: right;
		color: #000000;
		width: 80px;
		padding-left: 10px;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow-y: hidden;
	}
	&__footer {}
	&__link {
		.dinpro();
		.font-size(15);
		text-align: center;
		color: #ff3a1e;
		&:hover {
			text-decoration: none;
		}
	}
}