.section {
	&_slider {
		margin-bottom: 28px;
	}
	&_main-content {
		margin-bottom: 63px;
	}

	&__inner-wrap {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: flex-start;
	}
	&__col {
		&_main {
			flex-grow: 20;
			margin-right: 28px;
		}
		&_aside {
			width: 280px;
		}
	}
}