@fonts: "../fonts/";

// PT Sans
@import url('https://fonts.googleapis.com/css?family=PT+Sans:400,700&subset=cyrillic-ext');

// Happy Fox
@font-face {
	font-family: 'Happy Fox';
	src: url('@{fonts}happyfox.eot');
	src: url('@{fonts}happyfox.woff') format('woff'),
		 url('@{fonts}happyfox.ttf') format('truetype'),
		 url('@{fonts}happyfox.svg#happyfox') format('svg'),
		 url('@{fonts}happyfox.eot?#iefix') format('embedded-opentype');
	font-weight: normal;
	font-style: normal;
}

// DINPro MEDIUM
@font-face {
	font-family: 'DINPro';
	src: url('@{fonts}pfdintextpro-medium.eot');
	src: url('@{fonts}pfdintextpro-medium.woff2') format('woff2'),
		 url('@{fonts}pfdintextpro-medium.woff') format('woff'),
		 url('@{fonts}pfdintextpro-medium.ttf') format('truetype'),
		 url('@{fonts}pfdintextpro-medium.svg#pfdintextpro-medium') format('svg'),
		 url('@{fonts}pfdintextpro-medium.eot?#iefix') format('embedded-opentype');
	font-weight: 500;
	font-style: normal;
}

// DINPro BOLD
@font-face {
	font-family: 'DINPro';
	src: url('@{fonts}pfdintextpro-bold.eot');
	src: url('@{fonts}pfdintextpro-bold.woff2') format('woff2'),
		 url('@{fonts}pfdintextpro-bold.woff') format('woff'),
		 url('@{fonts}pfdintextpro-bold.ttf') format('truetype'),
		 url('@{fonts}pfdintextpro-bold.svg#pfdintextpro-bold') format('svg'),
		 url('@{fonts}pfdintextpro-bold.eot?#iefix') format('embedded-opentype');
	font-weight: 700;
	font-style: normal;
}

.regular() {
	font-family: 'PT Sans', sans-serif;
	font-weight: 400;
}
.bold() {
	font-family: 'PT Sans', sans-serif;
	font-weight: 700;
}
.fox() {
	font-family: 'Happy Fox', sans-serif;
	font-weight: 400;
}
.dinpro() {
	font-family: 'DINPro';
	font-weight: 700;
}
.dinpro-m() {
	font-family: 'DINPro';
	font-weight: 500;
}