.program-list {
	&__item {
		height: 443px;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50% 50%;
		position: relative;
		&:not(:last-child) {
			margin-bottom: 30px;
		}
	}
	&__label {
		width: 134px;
		height: 47px;
		background-color: #ff3a1e;
		position: absolute;
		top: 26px;
		left: 26px;
		z-index: 2;
		user-select: none;
		.fox();
		.font-size(33);
		line-height: 47px;
		letter-spacing: 2.8px;
		text-align: center;
		color: #ffffff;
	}
	&__link {
		text-decoration: none;
		display: inline-block;
		width: 100%;
		height: 100%;
	}
}