.btn {
	display: inline-block;
	text-decoration: none;
	user-select: none;
	background-color: #ff3a1e;
	transition: all .2s ease;
	min-width: 220px;
	height: 40px;
	padding: 0 15px;
	line-height: 43px;
	text-transform: uppercase;
	.dinpro();
	.font-size(15);
	letter-spacing: 1px;
	text-align: center;
	color: #ffffff;
	&:hover {
		background-color: #d43a24;
	}
	&:active {
		background-color: #a22311;
	}
	&_border {
		background-color: transparent;
		min-width: auto;
		.retina-sprite(@icon-btn-border-group);
		font-family: HappyFox;
		.fox();
		.font-size(27);
		line-height: 1.96;
		letter-spacing: 1.6px;
		text-align: center;
		color: #ff3a1e;
		&:hover {
			background-color: transparent;
		}
		&:active {
			background-color: transparent;
		}
	}
}