.news-block-list {
	&__item {
		padding: 20px 0;
		border-bottom: solid 1px rgba(151,151,151,.3);
		&:first-child {
			padding-top: 0;
		}
		&:last-child {
			border-bottom: 0;
			padding-bottom: 0;
		}
	}
	&__link {
		text-decoration: none;
	}
	&__title {
		.dinpro();
		.font-size(11);
		line-height: 1.18;
		letter-spacing: 0.6px;
		text-align: left;
		color: #ff3a1e;
		text-transform: uppercase;
		margin-bottom: 1px;
	}
	&__name {
		.bold();
		.font-size(15);
		font-weight: bold;
		line-height: 1.33;
		text-align: left;
		color: #000000;
		margin-bottom: 8px;
	}
	&__info {
		.regular();
		.font-size(14);
		line-height: 1.29;
		text-align: left;
		color: #747474;
		padding-right: 10px;
		height: 54px;
		overflow: hidden;
		position: relative;
		&:before {
			content: "â€¦";
			position: absolute;
			bottom: 0;
			right: 45px;
			color: #ea461b;
			z-index: 1;
		}
		&:after {
			content: "";
			box-sizing: border-box;
			width: 60px;
			height: 16px;
			padding-left: 5px;
			background-color: white;
			display: block;
			position: absolute;
			right: 0;
			bottom: 0;
		}
	}
}