.about-program {
	position: absolute;
	width: 240px;
	height: calc(~"100% - 26px * 2");
	top: 26px;
	right: 26px;
	z-index: 2;
	background: rgba(255,255,255,.9);
	padding: 26px 30px 30px;
	&__head {
		border-bottom: solid 1px rgba(151,151,151,.2);
		padding-bottom: 12px;
	}
	&__name {
		.dinpro();
		.font-size(12);
		text-transform: uppercase;
		line-height: 1.08;
		letter-spacing: 0.6px;
		text-align: left;
		color: #ea461b;
		margin-bottom: 8px;
	}
	&__name-count {
		.font-size(16);
		line-height: 0.81;
	}
	&__person-info {
		.bold();
		.font-size(15);
		line-height: 1.2;
		text-align: left;
		color: #000000;
	}
	&__body {
		border-bottom: solid 1px rgba(151,151,151,.2);
		padding: 12px 0;
	}
	&__diagnosis {
		.dinpro();
		.font-size(10);
		text-transform: uppercase;
		letter-spacing: 1.5px;
		text-align: left;
		color: #a2a2a2;
		margin-bottom: 6px;
	}
	&__diagnosis-info {
		.regular();
		.font-size(15);
		line-height: 1.2;
		letter-spacing: 0.2px;
		text-align: left;
		color: #000000;
		margin-bottom: 4px;
	}
	&__docs {}
	&__docs-link {
		.regular();
		.font-size(15);
		line-height: 1.2;
		text-align: left;
		color: #ff3a1e;
		&:hover {
			text-decoration: none;
		}
	}
	&__footer {
		padding-top: 12px;
	}
	&__footer-title {
		.regular();
		.font-size(15);
		text-align: left;
		color: #4e4e4e;
	}
	&__money {
		.bold();
		.font-size(15);
		line-height: 1.27;
		text-align: left;
		color: #000000;
		margin-bottom: 15px;
	}

	.btn {
		min-width: auto;
		display: block;
	}
}