.helped {
	margin-bottom: 40px;
	&__title {
		margin-bottom: 15px;
	}
	&__title-link {
		.dinpro();
		.font-size(17);
		line-height: 1.24;
		text-align: left;
		color: #000000;
		text-transform: uppercase;
		text-decoration: none;
	}
	&__items {
		margin-bottom: 3px;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-start;
	}
	&__item {
		width: 83px;
		height: 83px;
		background-color: #bbb5b5;
		margin-bottom: 17px;
	}
	&__item-link {
		text-decoration: none;
		display: block;
	}
	&__image {
		width: 100%;
		vertical-align: middle;
	}
	&__footer {}
	&__link {
		.dinpro();
		.font-size(15);
		text-align: left;
		color: #ff3a1e;
		&:hover {
			text-decoration: none;
		}
	}
	&__count {}
}