.main-slider {
	height: 262px;
	&__wrap {
		height: 100%;
	}
	&__slide {
		height: 100%;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: 50% 50%;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	&__title {
		.fox();
		.font-size(55);
		line-height: 1.33;
		letter-spacing: 0.7px;
		text-align: left;
		color: #ff3a1e;
		position: absolute;
		bottom: 36px;
		align-self: flex-start;
		margin-left: 143px;
	}
	&__arrow {
		position: absolute;
		top: 50%;
		display: block;
		z-index: 2;
		cursor: pointer;
		&_prev {
			left: 45px;
			margin-top: -@icon-prev-height / 2;
			.retina-sprite(@icon-prev-group);
			&:hover {
				.retina-sprite(@icon-prev-hover-group);
			}
		}
		&_next {
			right: 45px;
			margin-top: -@icon-prev-height / 2;
			.retina-sprite(@icon-next-group);
			&:hover {
				.retina-sprite(@icon-next-hover-group);
			}
		}
	}

	.btn {
		align-self: flex-end;
		margin-right: 176px;
	}
}