.news-block {
	&__title {
		margin-bottom: 30px;
	}
	&__title-link {
		.dinpro();
		.font-size(17);
		line-height: 1.12;
		text-align: left;
		color: #000000;
		text-decoration: none;
		text-transform: uppercase;
	}
	&__date {
		.regular();
		.font-size(12);
		text-align: left;
		color: rgba(0,0,0,.5);
		margin-left: 10px - 4px;
	}
	&__content {}
	&__footer {}
	&__all-news {}
}